'use client';

import React, {
  createContext,
  useContext,
  useState,
  useEffect,
  ReactNode,
} from 'react';
import { useLanguage } from '@/contexts/LanguageContext';

interface TranslationContextType {
  // ÁøªËØëÂäüËÉΩ
  translateText: (text: string, targetLang?: string) => Promise<string>;
  translateForm: (formData: any) => Promise<any>;
  autoTranslatePage: (
    onLanguageChange?: (newLang: string) => void
  ) => Promise<void>;

  // ÁøªËØëÁä∂ÊÄÅ
  isTranslating: boolean;
  translationCache: Map<string, string>;

  // Êô∫ËÉΩÁøªËØëËÆæÁΩÆ
  autoTranslate: boolean;
  setAutoTranslate: (enabled: boolean) => void;
  saveTranslation: (original: string, translated: string, lang: string) => void;
}

const TranslationContext = createContext<TranslationContextType | undefined>(
  undefined
);

export function TranslationProvider({ children }: { children: ReactNode }) {
  const { language } = useLanguage();
  const [isTranslating, setIsTranslating] = useState(false);
  const [translationCache, setTranslationCache] = useState<Map<string, string>>(
    new Map()
  );
  const [autoTranslate, setAutoTranslate] = useState(false);

  // ËØ≠Ë®ÄÊ£ÄÊµã
  const detectLanguage = (text: string): string => {
    const chineseChars = (text.match(/[\u4e00-\u9fff]/g) || []).length;
    const englishChars = (text.match(/[a-zA-Z]/g) || []).length;

    return chineseChars > englishChars ? 'zh' : 'en';
  };

  // Ëé∑ÂèñÊñáÊú¨ËäÇÁÇπ
  const getTextNodes = (element: Node): Text[] => {
    const textNodes: Text[] = [];
    const walker = document.createTreeWalker(element, NodeFilter.SHOW_TEXT, {
      acceptNode: node => {
        const parent = node.parentElement;
        if (
          parent &&
          (parent.tagName === 'SCRIPT' || parent.tagName === 'STYLE')
        ) {
          return NodeFilter.FILTER_REJECT;
        }
        return node.textContent && node.textContent.trim()
          ? NodeFilter.FILTER_ACCEPT
          : NodeFilter.FILTER_REJECT;
      },
    });

    let node;
    while ((node = walker.nextNode())) {
      textNodes.push(node as Text);
    }

    return textNodes;
  };

  // Âà§Êñ≠ÊñáÊú¨ÊòØÂê¶ÈúÄË¶ÅÁøªËØë
  const shouldTranslateText = (text: string): boolean => {
    // ËøáÊª§Êéâ‰∏çÈúÄË¶ÅÁøªËØëÁöÑÊñáÊú¨
    const skipPatterns = [
      /^\d+$/, // Á∫ØÊï∞Â≠ó
      /^\d{4}-\d{2}-\d{2}$/, // Êó•ÊúüÊ†ºÂºè
      /^[‚ù§Ô∏èüí¨üëÅÔ∏è‚ú®üë§‚Ä¢]+$/, // Á∫Øemoji
      /^[a-zA-Z0-9_]+$/, // Á∫ØËã±ÊñáÊï∞Â≠ó‰∏ãÂàíÁ∫øÔºàÁî®Êà∑ÂêçÁ≠âÔºâ
      /^[a-zA-Z0-9_]+_[0-9]+$/, // Â∏¶Êï∞Â≠óÁöÑÁî®Êà∑Âêç
      /^[‚Ä¢\s]+$/, // Á∫ØÁ©∫Ê†ºÂíåÁÇπ
      /^[0-9\s]+$/, // Á∫ØÊï∞Â≠óÂíåÁ©∫Ê†º
      /^[^\u4e00-\u9fff\w\s]+$/, // Á∫ØÁâπÊÆäÂ≠óÁ¨¶
      /^[a-zA-Z0-9_]+$/, // Á∫ØËã±ÊñáÊï∞Â≠ó‰∏ãÂàíÁ∫ø
      /^[0-9]+$/, // Á∫ØÊï∞Â≠ó
      /^[‚Ä¢]+$/, // Á∫ØÁÇπ
      /^[‚ù§Ô∏è]+$/, // Á∫ØÂøÉÂΩ¢
      /^[üí¨]+$/, // Á∫ØÂØπËØù
      /^[üëÅÔ∏è]+$/, // Á∫ØÁúºÁùõ
      /^[‚ú®]+$/, // Á∫ØÊòüÊòü
      /^[üë§]+$/, // Á∫ØÁî®Êà∑
    ];

    // Â¶ÇÊûúÊñáÊú¨Â§™Áü≠ÔºàÂ∞ë‰∫é2‰∏™Â≠óÁ¨¶ÔºâÔºå‰∏çÁøªËØë
    if (text.length < 2) {
      return false;
    }

    // Â¶ÇÊûúÂåπÈÖç‰ªª‰ΩïË∑≥ËøáÊ®°ÂºèÔºå‰∏çÁøªËØë
    for (const pattern of skipPatterns) {
      if (pattern.test(text)) {
        // // console.log(`Ë∑≥ËøáÁøªËØë: "${text}" (ÂåπÈÖçÊ®°Âºè: ${pattern})`)
        return false;
      }
    }

    // Â¶ÇÊûúÊñáÊú¨ÂåÖÂê´‰∏≠ÊñáÂ≠óÁ¨¶ÊàñËã±ÊñáÂ≠óÁ¨¶ÔºåÈúÄË¶ÅÁøªËØë
    const hasChinese = /[\u4e00-\u9fff]/.test(text);
    const hasEnglish = /[a-zA-Z]/.test(text);

    const shouldTranslate = hasChinese || hasEnglish;
    if (shouldTranslate) {
      // // console.log(`ÈúÄË¶ÅÁøªËØë: "${text}" (‰∏≠Êñá: ${hasChinese}, Ëã±Êñá: ${hasEnglish})`)
    }

    return shouldTranslate;
  };

  // ‰ªélocalStorageÂä†ËΩΩÁøªËØëÁºìÂ≠ò
  useEffect(() => {
    const cached = localStorage.getItem('translation_cache');
    if (cached) {
      try {
        const parsed = JSON.parse(cached);
        setTranslationCache(new Map(Object.entries(parsed)));
      } catch (error) {
        // // console.error('Failed to load translation cache:', error)
      }
    }
  }, []);

  // ‰øùÂ≠òÁøªËØëÁºìÂ≠òÂà∞localStorage
  const saveCacheToStorage = (cache: Map<string, string>) => {
    try {
      const cacheObj = Object.fromEntries(cache);
      localStorage.setItem('translation_cache', JSON.stringify(cacheObj));
    } catch (error) {
      // // console.error('Failed to save translation cache:', error)
    }
  };

  // ÁøªËØëÂçï‰∏™ÊñáÊú¨
  const translateText = async (
    text: string,
    targetLang?: string
  ): Promise<string> => {
    if (!text || !text.trim()) {
      return text;
    }

    const lang = targetLang || language;
    const cacheKey = `${text}_${lang}`;

    // Ê£ÄÊü•ÁºìÂ≠ò
    if (translationCache.has(cacheKey)) {
      return translationCache.get(cacheKey)!;
    }

    // Ê£ÄÊµãÊ∫êËØ≠Ë®ÄÔºå‰ΩÜ‰∏çÈòªÊ≠¢ÁøªËØë
    const detectedLang = detectLanguage(text);
    // // console.log(`Translating "${text}" from ${detectedLang} to ${lang}`)

    setIsTranslating(true);

    try {
      // ‰ºòÂÖà‰ΩøÁî®ÂêéÁ´ØÁøªËØëAPI
      const response = await fetch(
        'http://localhost:8002/api/translation/translate',
        {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            text,
            target_lang: lang,
            source_lang: 'auto',
          }),
        }
      );

      if (response.ok) {
        const result = await response.json();
        const translatedText = result.translated_text;

        // ÁºìÂ≠òÁªìÊûú
        const newCache = new Map(translationCache);
        newCache.set(cacheKey, translatedText);
        setTranslationCache(newCache);
        saveCacheToStorage(newCache);

        // // console.log(`Translation successful: "${text}" ‚Üí "${translatedText}"`)
        return translatedText;
      } else {
        // // console.error('Backend translation failed:', response.status)
        // ÂêéÁ´ØÁøªËØëÂ§±Ë¥•Ôºå‰ΩøÁî®ÂâçÁ´ØÁøªËØë
        return await translateWithFrontendAPI(text, lang);
      }
    } catch (error) {
      // // console.error('Backend translation failed, using frontend fallback:', error)
      return await translateWithFrontendAPI(text, lang);
    } finally {
      setIsTranslating(false);
    }
  };

  // ÂâçÁ´ØÁøªËØëAPIÔºàÂ§áÁî®ÊñπÊ°àÔºâ
  const translateWithFrontendAPI = async (
    text: string,
    targetLang: string
  ): Promise<string> => {
    try {
      // ËøôÈáåÂèØ‰ª•ÈõÜÊàêÂÖ∂‰ªñÁøªËØëÊúçÂä°ÔºåÂ¶ÇGoogle Translate API
      // ÁõÆÂâçËøîÂõûÊ®°ÊãüÁøªËØë
      if (targetLang === 'zh') {
        return `[‰∏≠ÊñáÁøªËØë: ${text}]`;
      } else {
        return `[English translation of: ${text}]`;
      }
    } catch (error) {
      // // console.error('Frontend translation failed:', error)
    }

    return text;
  };

  // ÁøªËØëË°®ÂçïÊï∞ÊçÆ
  const translateForm = async (formData: any): Promise<any> => {
    const translatedData = { ...formData };

    for (const [key, value] of Object.entries(formData)) {
      if (typeof value === 'string' && shouldTranslateText(value)) {
        try {
          const translated = await translateText(
            value,
            language === 'zh' ? 'en' : 'zh'
          );
          translatedData[key] = translated;
        } catch (error) {
          // ÁøªËØëÂ§±Ë¥•Êó∂‰øùÊåÅÂéüÂÄº
          console.warn(`Failed to translate form field ${key}:`, error);
        }
      }
    }

    return translatedData;
  };

  // Ëá™Âä®ÁøªËØëÈ°µÈù¢
  const autoTranslatePage = async (
    onLanguageChange?: (newLang: string) => void
  ): Promise<void> => {
    if (isTranslating) {
      return;
    }

    setIsTranslating(true);

    try {
      const targetLang = language === 'zh' ? 'en' : 'zh';
      const newLanguage = targetLang;

      // // console.log(`ÂºÄÂßãÂÖ®Â±ÄÁøªËØë: ${language} ‚Üí ${newLanguage}`)

      // Ëé∑ÂèñÈ°µÈù¢‰∏≠ÊâÄÊúâÈúÄË¶ÅÁøªËØëÁöÑÊñáÊú¨ËäÇÁÇπ
      const textNodes = getTextNodes(document.body);
      const textsToTranslate: { node: Text; text: string }[] = [];

      textNodes.forEach(node => {
        const text = node.textContent?.trim();
        if (text && shouldTranslateText(text)) {
          textsToTranslate.push({ node, text });
        }
      });

      // // console.log(`ÊâæÂà∞ ${textsToTranslate.length} ‰∏™ÂîØ‰∏ÄÊñáÊú¨ÈúÄË¶ÅÁøªËØë`)

      if (textsToTranslate.length === 0) {
        // Â¶ÇÊûúÊ≤°ÊúâÈúÄË¶ÅÁøªËØëÁöÑÊñáÊú¨ÔºåÁõ¥Êé•ÂàáÊç¢ËØ≠Ë®Ä
        if (onLanguageChange) {
          onLanguageChange(newLanguage);
        }
        return;
      }

      // ÊâπÈáèÁøªËØë
      try {
        const uniqueTexts = [
          ...new Set(textsToTranslate.map(item => item.text)),
        ];
        const response = await fetch(
          'http://localhost:8002/api/translation/translate_batch',
          {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({
              texts: uniqueTexts,
              target_lang: targetLang,
              source_lang: 'auto',
            }),
          }
        );

        if (response.ok) {
          const result = await response.json();
          const translationMap = new Map<string, string>();

          result.results.forEach((item: any) => {
            translationMap.set(item.original, item.translated);
          });

          // Êõ¥Êñ∞ÊâÄÊúâÊñáÊú¨ËäÇÁÇπ
          const allNodes = getTextNodes(document.body);
          allNodes.forEach(node => {
            const text = node.textContent?.trim();
            if (text && translationMap.has(text)) {
              const translatedText = translationMap.get(text);
              if (translatedText && translatedText !== text) {
                node.textContent = translatedText;
                // // console.log(`ÁøªËØë: "${text}" ‚Üí "${translatedText}"`)
              }
            }
          });

          // // console.log(`ÊâπÈáèÁøªËØëÂÆåÊàêÔºåÂÖ±ÁøªËØë ${result.results.length} ‰∏™ÊñáÊú¨`)
        } else {
          throw new Error('ÊâπÈáèÁøªËØëAPIÂ§±Ë¥•');
        }
      } catch (error) {
        // // console.error('ÊâπÈáèÁøªËØëÂ§±Ë¥•Ôºå‰ΩøÁî®ÂçïÊñáÊú¨ÁøªËØë:', error)

        // ÂõûÈÄÄÂà∞ÂçïÊñáÊú¨ÁøªËØë
        const batchSize = 10; // Â¢ûÂä†ÊâπÊ¨°Â§ßÂ∞è
        for (let i = 0; i < textsToTranslate.length; i += batchSize) {
          const batch = textsToTranslate.slice(i, i + batchSize);

          const translationPromises = batch.map(async ({ node, text }) => {
            try {
              const translatedText = await translateText(text, targetLang);
              if (translatedText && translatedText !== text) {
                // Êõ¥Êñ∞ÊâÄÊúâÁõ∏ÂêåÁöÑÊñáÊú¨ËäÇÁÇπ
                const allNodes = getTextNodes(document.body);
                allNodes.forEach(n => {
                  if (n.textContent?.trim() === text) {
                    n.textContent = translatedText;
                  }
                });
              }
            } catch (error) {
              // // console.warn(`ÁøªËØëÂ§±Ë¥•: "${text}"`, error)
            }
          });

          await Promise.all(translationPromises);

          // ÂáèÂ∞ëÂª∂Ëøü
          if (i + batchSize < textsToTranslate.length) {
            await new Promise(resolve => setTimeout(resolve, 50));
          }
        }
      }

      // ÈÄöÁü•ÁªÑ‰ª∂Êõ¥Êñ∞ËØ≠Ë®ÄÁä∂ÊÄÅ
      if (onLanguageChange) {
        onLanguageChange(newLanguage);
      }

      // // console.log('ÂÖ®Â±ÄÁøªËØëÂÆåÊàê')
    } catch (error) {
      // // console.error('ÂÖ®Â±ÄÁøªËØëÂ§±Ë¥•:', error)
      // Â¶ÇÊûúÁøªËØëÂ§±Ë¥•ÔºåÁõ¥Êé•ÂàáÊç¢ËØ≠Ë®Ä
      if (onLanguageChange) {
        const targetLang = language === 'zh' ? 'en' : 'zh';
        onLanguageChange(targetLang);
      }
    } finally {
      setIsTranslating(false);
    }
  };

  // ‰øùÂ≠òÁøªËØëÁªìÊûú
  const saveTranslation = (
    original: string,
    translated: string,
    lang: string
  ) => {
    const cacheKey = `${original}_${lang}`;
    const newCache = new Map(translationCache);
    newCache.set(cacheKey, translated);
    setTranslationCache(newCache);
    saveCacheToStorage(newCache);
  };

  // Ëá™Âä®ÁøªËØëÂäüËÉΩ
  useEffect(() => {
    if (autoTranslate) {
      autoTranslatePage();
    }
  }, [language, autoTranslate]);

  const value: TranslationContextType = {
    translateText,
    translateForm,
    autoTranslatePage,
    isTranslating,
    translationCache,
    autoTranslate,
    setAutoTranslate,
    saveTranslation,
  };

  return (
    <TranslationContext.Provider value={value}>
      {children}
    </TranslationContext.Provider>
  );
}

export function useTranslation() {
  const context = useContext(TranslationContext);
  if (context === undefined) {
    throw new Error('useTranslation must be used within a TranslationProvider');
  }
  return context;
}
